<template>
  <div id="app">
    <h1>üôè Íµ¨Ïó≠ ÏãùÍµ¨Îì§Ïùò Ï∂ïÌïò ÎßàÏùå</h1>

    <div class="input-form">
      <input v-model="newItem.writer" placeholder="Ïù¥Î¶Ñ" />
      <input
        v-model="newItem.content"
        @keyup.enter="sendMessage"
        placeholder="Ï∂ïÌïò Î©îÏãúÏßÄ (ÏóîÌÑ∞!)"
      />
      <button @click="sendMessage">Î≥¥ÎÇ¥Í∏∞</button>
    </div>

    <div class="bubble-container">
      <div
        v-for="(msg, index) in messages"
        :key="index"
        class="bubble"
        :style="msg.style"
      >
        <strong>{{ msg.WRITER }}</strong>
        <p>{{ msg.CONTENT }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      messages: [],
      newItem: { writer: "", content: "" },
      apiUrl: "https://yulha.duckdns.org/api/messages", // Î∞±ÏóîÎìú Ï£ºÏÜåÎ°ú Î≥ÄÍ≤Ω
    };
  },
  methods: {
    // 1. Î©îÏãúÏßÄ Î∂àÎü¨Ïò§Í∏∞
    async fetchMessages() {
      try {
        const res = await axios.get(this.apiUrl);
        // Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏Ïò® ÌõÑ Í∞ÅÍ∞Å ÎûúÎç§ Ïä§ÌÉÄÏùº Î∂ÄÏó¨
        this.messages = res.data.map((m) => ({
          ...m,
          style: this.getRandomStyle(),
        }));
      } catch (err) {
        console.error("Îç∞Ïù¥ÌÑ∞ Î°úÎî© Ïã§Ìå®:", err);
      }
    },
    // 2. Î©îÏãúÏßÄ Î≥¥ÎÇ¥Í∏∞
    async sendMessage() {
      if (!this.newItem.writer || !this.newItem.content) return;
      await axios.post(this.apiUrl, {
        writer: this.newItem.writer,
        content: this.newItem.content,
      });
      this.newItem.content = ""; // ÏûÖÎ†•Ï∞Ω ÎπÑÏö∞Í∏∞
      this.fetchMessages(); // Î™©Î°ù Í∞±Ïã†
    },
    // 3. ÎûúÎç§ ÏúÑÏπò Î∞è ÏÉâÏÉÅ ÏÉùÏÑ±
    getRandomStyle() {
      const top = Math.floor(Math.random() * 70) + 10; // 10% ~ 80% ÏÇ¨Ïù¥
      const left = Math.floor(Math.random() * 80) + 5; // 5% ~ 85% ÏÇ¨Ïù¥
      const colors = ["#FFD1DC", "#CAF0F8", "#E2F0CB", "#FFEFD5", "#E6E6FA"];
      const color = colors[Math.floor(Math.random() * colors.length)];

      return {
        top: `${top}%`,
        left: `${left}%`,
        backgroundColor: color,
      };
    },
  },
  mounted() {
    this.fetchMessages();
  },
};
</script>

<style>
.bubble-container {
  position: relative;
  width: 100%;
  height: 80vh; /* ÌíçÏÑ†Ïù¥ ÎÜÄ Ïàò ÏûàÎäî Í≥µÍ∞Ñ */
  overflow: hidden;
  background-color: #f9f9f9;
}

.bubble {
  position: absolute;
  padding: 15px 20px;
  border-radius: 20px;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.1);
  max-width: 200px;
  animation: float 3s ease-in-out infinite;
  transition: all 0.5s ease;
}

/* Îë•Ïã§Îë•Ïã§ Îñ†Îã§ÎãàÎäî Ìö®Í≥º */
@keyframes float {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

.input-form {
  margin-bottom: 20px;
  text-align: center;
}
</style>
